export function HomeHook(responses){
    return {
        DOM: responses.DOM.select('.myinput').events('input')
            .map(ev => ev.target.value)
            .startWith('')
            .map(name =>
                h('div', [
                    h('label', 'Name:'),
                    h('input.myinput', {attributes: {type: 'text'}}),
                    h('hr'),
                    h('h1', `Hello ${name}`)
                ])
        )
    };
}